:root {
  --button-color: #fa8142;
  --text-color: #ccc;
  --hover-text-color: #fff;
  --background-color: #1f1f1f;
  --text-gray-300: #cbd5e1;
  --text-gray-500: #6b7280;
  --gradient-color-1: #fa8142;
  --gradient-color-2: #ff4088;
  --gradient-angle: 270deg;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background: var(--background-color);
  color: var(--text-color);
}

.dark {
  --button-color: #fa8142;
  --text-color: #ccc;
  --hover-text-color: #fff;
  --background-color: #1f1f1f;
  --text-gray-300: #cbd5e1;
  --text-gray-500: #6b7280;
}

.light {
  --background-color: #fff;
  --text-color: #333;
  --hover-text-color: #000;
  --text-gray-300: #ccc;
  --text-gray-500: #a0aec0;
}

::-moz-selection {
  background: #ff8c5a;
  color: #fff;
}

::selection {
  background: #ff8c5a;
  color: #fff;
}

a {
  text-decoration: none;
  color: var(--text-color);
}

::-webkit-scrollbar {
  background-image: initial;
  background-color: transparent;
  height: 8px;
  width: 8px;
  background-color: #202324;
  color: #aba499;
}

::-webkit-scrollbar-corner {
  background-color: #181a1b;
}

::-webkit-scrollbar-thumb {
  border-color: initial;
  background-image: initial;
  background-color: #555;
  border: none;
  box-shadow: none;
  border-radius: 4px;
  min-height: 40px;
}

::-webkit-scrollbar-thumb:hover {
    background: #404344;
  }

::-webkit-scrollbar-thumb:active {
    background: #4a4c4d;
  }

::-webkit-scrollbar-track {
  margin: 3px 0;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  width: 100vw;
  height: 70px;
  background: linear-gradient(
    161.94deg,
    rgb(255, 140, 90) 7.34%,
    rgb(255, 84, 213) 21.14%,
    rgb(128, 142, 253) 68.19%,
    rgb(107, 215, 204) 96.01%
  );
  font-size: 1.4em;
}

.nav-item {
  padding: 0.2em 0.5em;
  text-transform: uppercase;
  color: white;
}

.nav-item:hover {
    cursor: pointer;
    color: var(--hover-text-color);
  }

.gradient-text {
  background-image: linear-gradient(
    var(--gradient-angle),
    var(--gradient-color-1),
    var(--gradient-color-2)
  );
  -webkit-background-clip: text;
          background-clip: text;
  color: transparent;
  padding-bottom: 0.6rem;
}

.header h1 {
    width: 350px;
  }

.header h2 {
    --gradient-color-1: #00aaff;
    --gradient-color-2: #00ff6c;
    --gradient-angle: 90deg;
    width: 275px;
  }

.about img {
    width: 300px;
    border-radius: 10%;
    filter: grayscale(100%);
    transform: translate(75%, -75%);
    margin-top: 1.5rem;
  }

.about img:hover {
      filter: grayscale(0%);
    }

.about p {
    width: clamp(50ch, 50%, 75ch);
  }

.contact a:hover {
    color: var(--hover-text-color);
  }

.contact-links img {
    width: 3em;
  }

.space-left {
  transform: translate(300px, 50px);
}

.contact-imgs {
  margin-top: 30px;
  width: 21em;
  border: 3.5px solid #062b44;
  border-radius: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-imgs img {
    width: 4em;
    padding: 20px 10px;
  }

.contact-imgs img:hover {
      transition: transform 0.2s ease-in;
    }

.contact-imgs .stack-overflow-image {
    width: 3.5em;
  }

.img-container {
  margin: 10px auto;
  width: 6em;
  height: 7.5em;
  padding: 10px;
  border: 2px solid #56a5eb;
  border-radius: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.img-container p {
    padding-top: 4px;
  }

.img-container:hover {
    box-shadow: 0 0.4rem 1.4rem 0 rgba(86, 185, 235, 0.9);
    transform: translateY(-0.1rem);
    transition: transform 150ms;
  }

.projects img {
    width: 75em;
  }

.button {
  border: 2px solid var(--button-color);
}

.button:hover {
    transform: translateY(-0.1rem);
    transition: background-color 0.4s ease;
  }

.button:active {
    transform: scale(0.98);
  }

.imgs-grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  justify-content: space-between;
  max-width: 700px;
  place-items: center;
  margin: 10px auto;
}

.footer a {
    color: var(--text-gray-500);
  }

.footer a:hover {
      color: var(--text-gray-300);
    }

.dark-light-toggle-container {
  position: absolute;
}

@media (max-width: 768px) {
  .navbar {
    display: none;
  }

  .imgs-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .header {
    display: flex;
    flex-direction: column;
    margin: 40px 0;
    padding: 0;
    transform: translate(-300px, -50px);
    text-align: center;
  }

    .header h1 {
      width: 100vw;
      display: flex;
      justify-content: center;
    }

  .about {
    margin-top: 75px;
  }

    .about img {
      display: none;
    }

    .about p {
      font-size: 0.8em;
    }
    .projects p {
      font-size: 0.9em;
    }

    .projects img {
      width: 30em;
    }

  .contact-imgs {
    width: 100vw;
    display: flex;
    justify-content: center;
  }

  .repo {
    display: none;
  }

  .dark-light-toggle-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
